import React                                        from 'react';
import PropTypes                                    from 'prop-types';



/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';












/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';









import './s.bankCode.step3.css';







export default class ComponentBankCodeStep3 extends ComponentAbstract {




    static propTypes = {
        data        : PropTypes.object

        , form      : PropTypes.object

        , result      : PropTypes.object


        , onClickNext : PropTypes.func
        , onClickPrev : PropTypes.func
    }


    static defaultProps = {
        data        : {}

        , form      : {}

        , result      : {}


        , onClickNext : () => {}
        , onClickPrev : () => {}
    }











    constructor( props ) {
        super( props )
    }










    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {
        return (
            <div>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } >
                            Bank Code
                        </Col>

                        <Col md={ 10 } >
                            { this.props.form.bankCode }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } >
                            Bank Name
                        </Col>

                        <Col md={ 10 } >
                            { this.props.form.bankName }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } >
                            IBG Routing Number
                        </Col>

                        <Col md={ 10 } >
                            { this.props.form.routingNumber }
                        </Col>
                    </Row>
                </Grid>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } >
                            RPP Code
                        </Col>

                        <Col md={ 10 } >
                            { this.props.form.rppCode }
                        </Col>
                    </Row>
                </Grid>




                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 12 } >
                            <div
                                    className={ ' bankCode-step1-textAlign-center bankCode-step1-paddingBottom-10 ' }>
                                <RaisedButton
                                                   label={ "Done" }
                                                   onClick={ () => {
                                                       this.props.onClickNext();
                                                   } }
                               />

                           </div>
                        </Col>
                    </Row>
                </Grid>

            </div>
        )
    }
}



