import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionBankCode , { ReduxEnumBankCode }  from '../../../_redux/action/reduxAction.bankCode';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';










/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';

import ComponentBankCodeStep1                       from './step1/c.bankCode.step1';
import ComponentBankCodeStep2                       from './step2/c.bankCode.step2';
import ComponentBankCodeStep3                       from './step3/c.bankCode.step3';








import './s.bankCode.detail.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxBankCode' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumBankCode.INIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestListDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];


                        } else if ( ReduxEnumBankCode.UPDATE_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdatePreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumBankCode.UPDATE_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];


                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentBankCodeDetail extends ComponentAbstract {


            constructor( props ) {
                super( props )

                this.state = {

                    step : 0

                    , data : {}


                    , form : {
                        bankCode : ''
                        , bankName : ''
                        , routingNumber : ''
                        , rppCode : ''
                    }


                    , result : {
                        preview : {}

                        , confirmation : {}
                    }

                }

            }













            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestUpdatePreview() {
                console.log( 'ComponentBankCodeDetail : requestUpdatePreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBankCode.rUpdatePreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            bankCode            : this.state.form.bankCode
                                            , bankName          : this.state.form.bankName
                                            , routingNumber     : this.state.form.routingNumber
                                            , rppCode           : this.state.form.rppCode
                                        }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdatePreviewDone( result ) {
                console.log( 'ComponentBankCodeDetail : requestUpdatePreviewDone : ***  : result => ' , result );

                let resultObj = this.state.result;
                resultObj[ 'preview' ] = result;
                this.setState(
                    {
                        isLoading : false
                        , result : resultObj

                        , step : 1
                    }
                );
                this.dispatch( ReduxActionBankCode.rRequestEnd( ) );
            }

            requestUpdateConfirm() {
                console.log( 'ComponentBankCodeDetail : requestUpdateConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionBankCode.rUpdateConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            instructionId : this.state.result.preview.instructionId
                                        }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdateConfirmDone( result ) {
                console.log( 'ComponentBankCodeDetail : requestUpdateConfirmDone : ***  : result => ' , result );

                let resultObj = this.state.result;
                resultObj[ 'confirm' ] = result;
                this.setState(
                    {
                        isLoading : false
                        , result : resultObj

                        , step : 2
                    }
                );
                this.dispatch( ReduxActionBankCode.rRequestEnd( ) );
            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>
                            <Paper
                                    style={ { width : '70%', margin : '0px auto' } }>


                                <div className={ ' bankCode-detail-padding-10 ' }>
                                    <h3>Bank Code</h3>
                                </div>


                                <div className={ ' bankCode-detail-margin-auto bankCode-detail-paddingTop-20 bankCode-detail-paddingBottom-20  bankCode-detail-maxWidth-900 ' }>

                                    <Stepper activeStep={ this.state.step }>

                                        <Step>
                                            <StepLabel>
                                                Bank Code
                                            </StepLabel>
                                        </Step>

                                        <Step>
                                            <StepLabel> Preview </StepLabel>
                                        </Step>

                                        <Step>
                                            <StepLabel> Confirmation </StepLabel>
                                        </Step>

                                    </Stepper>

                                </div>


                                <div>

                                    {
                                        ( () => {

                                            switch ( this.state.step ) {

                                                case 0:

                                                    return (
                                                        <ComponentBankCodeStep1
                                                            data={ this.state.data }

                                                            form={ this.state.form }

                                                            onChangeForm={ ( key , value ) => {
                                                                let form = this.state.form;
                                                                form[ key ] = value;
                                                                this.setState( { form : form } );
                                                            } }

                                                            onClickNext={ () => {
                                                                this.requestUpdatePreview();
                                                            } }

                                                            onClickPrev={ () => {

                                                            } }
                                                        />
                                                    );

                                                    break;

                                                case 1:

                                                    return (
                                                        <ComponentBankCodeStep2
                                                            data={ this.state.data }

                                                            form={ this.state.form }

                                                            result={ this.state.result }

                                                            onClickNext={ () => {
                                                                this.requestUpdateConfirm();
                                                            } }

                                                            onClickPrev={ () => {
                                                                this.setState( { step : 0 } );
                                                            } }
                                                        />
                                                    );

                                                    break;


                                                case 2:

                                                    return (
                                                        <ComponentBankCodeStep3
                                                            data={ this.state.data }

                                                            form={ this.state.form }

                                                            result={ this.state.result }

                                                            onClickNext={ () => {
                                                                HelperNavigate.route(
                                                                    HelperNavigate.mapPath( HelperNavigateENUM.BANK_CODE_LIST )
                                                                );
                                                            } }

                                                            onClickPrev={ () => {

                                                            } }
                                                        />
                                                    );

                                                    break;

                                                default:
                                                    return ( null );
                                            }

                                        } )()
                                    }

                                </div>

                            </Paper>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />

                        </div>
                )
            }
        }

    )



